<template>
  <div class="wrapper" ref="wrapper">
      <div>
        <div v-if="pullDownRefresh" class="pulldown">
          <div v-show="showLoding" class="loading">
           <p></p>
          </div>
        </div>
        <slot></slot>
        <div v-if="pullUpLoad" class="pullup">
          <div class="clear" v-if="!isDone && isLoading">
            <div class="loading">.....</div>
          </div>
          <div class="list-donetip" v-if="!isLoading && isDone">
            <p class="loading"></p>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import BetterScroll from 'better-scroll'


export default {
  name: "Scroll",
  components: {
    // BetterScroll
  },
  props: {
    probeType: {
      type: Number,
      default: 0
    },
    pullUpLoad: {
      type: Boolean,
      default: false
    },
    pullDownRefresh: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      scroll: null,
      showLoding: false,
      isLoading: false,
      isDone: false
    }
  },
  mounted() {
    console.log(this.$refs.wrapper)
    this.scroll = new BetterScroll(this.$refs.wrapper, {
      movable: true,
      zoom: true,
      probeType: this.probeType,
      pullUpLoad: this.pullUpLoad,
      pullDownRefresh: this.pullDownRefresh
    })

    //
    if(this.probeType ===2 || this.probeType === 3) {
      //
      this.scroll.on('scroll',(postion)=>{
        // console.log(postion);
        this.$emit('scroll',postion)
      })
    }
    if(this.pullUpLoad) {
      this.scroll.on('pullingUp',()=> {
        // console.log("");
        if(this.scroll.y <= (this.scroll.maxScrollY + 5)) {
          //
          this.$on('infinitescroll.loadedDone', () => {
            this.isLoading = false;
            this.isDone = true;
            console.log("1");
          });
          //
          this.$on('infinitescroll.loading', () => {
            this.isLoading = true;
            // console.log("2");
          });
          //
          this.$on('infinitescroll.reInit', () => {
            this.isLoading = false;
            this.isDone = false;
            console.log("3");
          });
          // console.log("4");
          this.$emit('pullingUp')
        }
      })
    }
    if(this.pullDownRefresh) {
      this.scroll.on('pullingDown',()=> {
        this.$on('infinitescroll.downloading', () => {
          this.showLoding = true;
          // console.log("2");
        });
        this.$on('infinitescroll.downover', () => {
          this.showLoding = false;
          // console.log("2");
        });
          console.log("");
          console.log(this.showLoding);
        // console.log("");
        this.$emit('pullingDown')
      })
    }
  },
  methods: {
    scrollTo(x, y, time = 300) {
      //
      this.scroll && this.scroll.scrollTo(x, y, time)
    },
    finishPullUp() {
      this.scroll && this.scroll.finishPullUp()
    },
    finishPullDown() {
      this.scroll && this.scroll.finishPullDown()
    },
    refresh() {
      console.log('-----')
      this.scroll && this.scroll.refresh()
    },
    getScrollY() {
      return this.scroll ? this.scroll.y : 0
    }
  }


}
</script>

